img{
    width: 25px;
    height: 25px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    transition: all 0.5s ease-in-out;
}

.TechListFlex{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 10px;
    overflow-x: ellipsis;

}
.TechList{
    background-color: rgba(32, 31, 31, 0.468);
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
}
h1{
    font-size: 10px;
}